.intro-section-text {
    margin: 10px 100px 10px 100px;
}

.hello-text {
    margin-top: 80px;
    font-size: 20px;
}

.arun-text {
    font-size: 80px;
    color: #ccd6f6;
    font-weight: 200px;
    border-bottom: solid 1px white;
}

.problem-text {
    font-size: 80px;
    color: #8892b0;
    font-weight: 200px;
}

.intro-description {
    width: 70%;
    font-size: 20px;
    margin-bottom: 50px;
}

.btn-contact {
    font-size: 30px;
    color: #E09DF5;
    border: solid 1px #E09DF5;
    padding: 10px;
    border-radius: 10px;
    text-decoration: none;
    transition: color 0.3s;
    transition: background 0.3s;
}

.btn-contact-header {
    margin-left: 100px;
}

.btn-contact:hover {
    color: white;;
    background: #E09DF5;
}

.portrait-image {
    border-radius: 200px;
    width: 250px;
}

.tech-list {
    display: flex;
    justify-content: space-around;
}

.about-text {
    font-size: 18px;
}

.about-middle-text {
    font-size: 22px;
    color: #E09DF5;
}

.contact-text {
    color: #703A8F;
    font-weight: bold;
    font-size: 20px;
    margin: 50px 0px;
    text-align: center;
}

.about-section-text {
    width: 70%;
}

@media screen and (max-width: 1024px) {
    .arun-text {
        font-size: 70px;
    }
    .problem-text {
        font-size: 70px;
    }
    .intro-section-text {
        margin: 10px 100px 10px 80px;
    }
    .intro-description {
        margin-bottom: 70px;
    }
    .btn-contact-header {
        margin-left: 80px;
    }
    .about-section-text {
        width: 100%;
        margin-top: 20px;
    }
}

@media screen and (max-width: 750px) {
    .arun-text {
        font-size: 64px;
    }
    .problem-text {
        font-size: 58px;
    }
}

@media screen and (max-width: 600px) {
    .arun-text {
        font-size: 56px;
    }
    .problem-text {
        font-size: 50px;
    }
    .about-text {
        font-size: 16px;
    }
}
@media screen and (max-width: 400px) {
    .intro-section-text {
        margin: 10px 100px 30px 50px;
    }
    .btn-contact-header {
        margin-left: 50px;
    }
    .arun-text {
        font-size: 48px;
    }
    .problem-text {
        font-size: 30px;
    }
    .about-text {
        font-size: 12px;
    }
}
